
.member-image {
  width: 200px;
  height: 200px;
  margin: auto;
  display: block;
}
.member-photo {
  width: 200px;
  height: 200px;
  position: absolute;
}
.member-map {
  visibility: hidden;
  position: absolute;
}
.member-image:hover .member-photo {
  visibility: hidden;
}
.member-image:hover .member-map {
  visibility: visible;
}



